@mixin r($width) {
  @media only screen and (max-width: $width + 'px') {
    @content; } }


@mixin f-ttf($alias, $font-name) {
  @font-face {
    font-family: $alias;
    src:  url("/fonts/" + $font-name + ".ttf"); } }


@mixin f-woff($alias, $font-name) {
  @font-face {
    font-family: $alias;
    src:  url("/fonts/" + $font-name + ".woff2") format('woff2') {
          url("/fonts/" + $font-name + ".woff") format('woff') {} } } }
