*::selection {
  background-color: transparent;
  color: #FFA00C; }

.wrapper {
  width: 100%;
  height: 100vh;
  background-color: #212121;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.welcome {
  display: flex;
  align-items: center;
  flex-grow: 1;
  color: #ffffff;
  font-size: 216px;
  font-family: 'Amatic SC'; }

.footer {
  min-height: 100px; }

a.fancy {
  font-family: 'Amatic SC';
  letter-spacing: 5px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: block;
  position: relative;
  &:hover {
    &::before {
      top: 100%;
      opacity: 1; } }
  &::before {
    border-radius: 3px;
    transition: all .2s ease-out;
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    opacity: 0;
    top: 150%;
    background-color: #D88F0D; } }

.fancy-link-should-be-removed {
  background-color: #ffffff; }
